<template>
  <div id="dashboard_patient" v-if="patient">

    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs12 md4>

          <v-card>
            <v-card-title class="pb-0">
              <label class="ash--text mr-2">id</label>
              <label>{{patient.id}}</label>
              <p class="headline">{{patient.name}}</p>
            </v-card-title>
            <v-card-text class="pt-0">
              <label class="primary_d--text">age</label>
              <label>{{patient.age}} years</label><br>
              
              <div id="dashboard_patient_div">
                <label class="primary_d--text">vitabox</label>
                <v-btn id="dashboard_patient_icon" flat icon small color="error" @click.native="()=>dialog_remove_patient=true"><v-icon>close</v-icon></v-btn>
                <p>{{box.address}}</p>
              </div>

              <div id="dashboard_patient_div" class="mb-3">
                <label class="primary_d--text">activity status</label>
                <v-btn id="dashboard_patient_icon" class="mt-0" flat icon small @click.native="()=>dialog_disable_patient=true">
                  <v-icon v-if="patient.active" color="success" dark>check_box</v-icon>
                  <v-icon v-else color="error" dark>check_box_outline_blank</v-icon>
                </v-btn>
              </div>
              
              <v-divider></v-divider>
              <set-board-to-patient :patient="patient" :box="box" @added="(item)=>boardAdded(item)"></set-board-to-patient>
              <v-layout row wrap>
                <v-flex v-for="board in boards" :key="board.id" xs6 md12>
                  <v-btn block color="secondary" class="my-0" dark @click="()=>{selected_board=board;selected_sensor='tab-'+board.Sensors[0].id}">{{board.Boardmodel.name}}</v-btn>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>

        <v-flex xs12 md8>
          <div v-if="selected_board">
            <v-tabs centered grow slider-color="primary_d" color="primary" v-model="selected_sensor">
              <v-tab v-for="sensor in selected_board.Sensors" :key="sensor.id" :href="`#tab-${sensor.id}`">
                <label>{{sensor.Sensormodel.transducer}} - {{sensor.Sensormodel.measure}}</label>
              </v-tab>
            </v-tabs>
            <v-tabs-items v-model="selected_sensor">
              <v-tab-item v-for="sensor in selected_board.Sensors" :key="sensor.id" :id="`tab-${sensor.id}`">
                <sensor-graph :sensor="sensor" :patient="patient.id"></sensor-graph>
              </v-tab-item>
            </v-tabs-items>
          </div>
        </v-flex>
      </v-layout>
    </v-container>

    <v-dialog v-model="dialog_remove_patient" max-width="500px">
      <remove-patient-from-vitabox @close="()=>dialog_remove_patient=false" @remove="remove" :patient="patient" :box="box"></remove-patient-from-vitabox>
    </v-dialog>
    <v-dialog v-model="dialog_disable_patient" max-width="500px">
      <disable-patient @close="()=>dialog_disable_patient=false" @disable="disable" :patient="patient" :box="box" :action="patient.active?'disable':'enable'"></disable-patient>
    </v-dialog>

  </div>
</template>

<script>
import { event_bus } from "@/plugins/bus.js";
import SensorPatient from "./SensorPatient.vue";
import RemovePatient from "../vitabox/RemovePatient.vue";
import DisablePatient from "../vitabox/DisablePatient.vue";
import SetBoardToPatient from "../vitabox/SetBoardToPatient.vue";

export default {
  name: "dashboard_patient",
  props: {
    patient: Object,
    box: Object
  },
  data: () => {
    return {
      boards: [],
      sensors: [],
      dialog_remove_patient: false,
      dialog_disable_patient: false,
      selected_board: null,
      selected_sensor: null
    };
  },
  mounted() {
    if (this.patient) {
      event_bus.$emit("waiting", 20);
      event_bus.$data.http
        .get("/patient/" + this.patient.id + "/board")
        .then(response => {
          this.boards = response.data.boards;
          this.sensors = this.boards.length > 0 ? this.boards[0].Sensors : [];
          event_bus.$emit("waiting", 80);
        })
        .catch(error => {
          if (error.response) {
            event_bus.$emit("toast", {
              message: error.response.data,
              type: "error"
            });
          } else {
            event_bus.$emit("toast", { message: error.message, type: "error" });
          }
          event_bus.$emit("waiting", 80);
        });
    } else {
      event_bus.$emit("toast", { message: "Patient undefined", type: "error" });
    }
  },
  methods: {
    formatDate(date) {
      let monthNames = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec"
      ];
      let d = new Date(date);
      return (
        d.getDate() + " " + monthNames[d.getMonth()] + " " + d.getFullYear()
      );
    },
    getTime(date) {
      let miliseconds = new Date() - new Date(date);
      if (miliseconds < 1000) return "<1 sec";
      else if (miliseconds < 60000) {
        return "~" + Math.floor(miliseconds / 1000) + " sec";
      } else if (miliseconds < 3600000) {
        return "~" + Math.floor(miliseconds / 60000) + " min";
      } else if (miliseconds < 86400000) {
        return Math.floor(miliseconds / 3600000) + " hours";
      } else return ">24 hours";
    },
    remove() {
      this.dialog_remove_patient = false;
      this.$router.push("/vitabox");
    },
    disable() {
      this.dialog_disable_patient = false;
      this.patient.active = !this.patient.active;
    },
    boardAdded(id) {
      event_bus.$emit("waiting", 20);
      event_bus.$data.http
        .get("/board/" + id)
        .then(response => {
          this.boards.push(response.data.board);
          event_bus.$emit("waiting", 30);
        })
        .catch(error => {
          if (error.response) {
            event_bus.$emit("toast", {
              message: error.response.data,
              type: "error"
            });
          } else {
            event_bus.$emit("toast", { message: error.message, type: "error" });
          }
          event_bus.$emit("waiting", 30);
        });
    }
  },
  components: {
    "sensor-graph": SensorPatient,
    "remove-patient-from-vitabox": RemovePatient,
    "disable-patient": DisablePatient,
    "set-board-to-patient": SetBoardToPatient
  }
};
</script>

<style>
#dashboard_patient_div {
  position: relative;
  width: 100%;
}
#dashboard_patient_icon {
  position: absolute;
  right: 0;
}
</style>


